:root {
  --tag-padding: 10px;
  --tag-position: 20px;
}

.tile {
  border-radius: 10px;
  padding: var(--tag-padding);
  position: relative;
  background-color: rgba(255, 255, 255, 0.1);
  animation: tile-fade 500ms ease-out;
}

.project-title {
  z-index: 10;
  margin: auto;
  position: absolute;
  top: 50%;
  left: var(--tag-position);
  text-align: center;
}

.tile-img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  display: block;
  border-radius: 5px;
  transition: transform 700ms, filter 1000ms;
  filter: brightness(50%);
}

.tile-img:hover {
  transform: scale(1.05);
  filter: brightness(100%);
}

.tile:hover .project-title {
  visibility: hidden;
}

.tag {
  border-radius: 5px;
  background-color: #121212;
  opacity: 0.7;
  color: #fff;
  padding: var(--tag-padding);
  width: 30%;
  position: absolute;
  top: var(--tag-position);
  left: var(--tag-position);
  z-index: 5;
  border: 1px solid #fff;
}

.languageTag {
  border-radius: 5px;
  background-color: #121212;
  opacity: 0.7;
  padding: 10px;
  color: #fff;
  border: 1px solid #fff;
}

.langTagContainer {
  position: absolute;
  bottom: var(--tag-position);
  left: var(--tag-position);
  display: flex;
  gap: 10px;
}

@keyframes tile-fade {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}
